.About{
    padding :  2rem;
    display: flex;
    /* grid-template: 2rem; */
    gap:2rem;
}

.left-r{
    /* flex:1 1;
    display: grid;
    grid-template-rows: repeat(2,1fr);
    gap:1rem;
    grid-auto-rows: 1fr; */
    display: flex;
    flex-direction: column;
    gap:2rem;
}

.left-r>img{
    object-fit: cover;
    width: 30rem;
}
.left-r>:nth-child(2){
    object-fit: cover;

}

.right-r{
    flex:1 1;
    text-transform: uppercase;
    gap:1rem;
    display: flex;
    flex-direction: column;
}
.right-r>span{
    font-weight: bold;
    color:var(--orange);
}
.right-r>div{
    color: white;
    font-size: 2rem;
    font-weight: bold;
}
.details-r{
    display: flex;
    flex-direction: column;
    gap:1rem;
}

.details-r>div{
    display: flex;
    font-size: 1rem;
    gap:1rem;
}
.details-r>div>img{
    width: 2rem;
    height: 2rem;
}
.our-partners-text{
    color:var(--gray);
    font-weight: bold;
    font-size: 2rem;
}
.partners{
    display: flex;
    gap:1rem;
}

.partners>img{
    width: 2.5rem;;
}

@media screen and (max-width:708px){
.About{
    flex-direction: column;
}
.left-r{
    grid-auto-rows: auto;
    overflow: hidden;
}
.left-r>:nth-child(1){
    width: 7rem;
    height: 17rem;
}
.left-r>:nth-child(2){
    width: 15rem;
    height: 10rem;
}.left-r>:nth-child(3){
    width: 7rem;
}
.left-r>:nth-child(4){
    width: 7rem;
    height: 6rem;
}
.right-r{
    /* text-wrap: wrap; */
}

}